import React from 'react';

class Bubble extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            popup: false
        };
        this.togglePopup = this.togglePopup.bind(this)
    }

    togglePopup() {
        this.setState({
            popup: !this.state.popup
        });
    }

    renderPopUp() {
        return (
            <div className='popup'>
                <div className='popup_content'>
                    <h1>{this.props.title}</h1>
                    <button onClick={this.togglePopup}>Close</button>
                </div>
            </div>
        )
    }

    render() {
        return (
            <div>
                <div className="bubble">
                    <header className="bubble-head"><b>{this.props.title}</b></header>
                    <p className="buble-content">
                        {this.props.content}
                    </p>
                    <div>
                        <button onClick={this.togglePopup} className="continue"><b>* * * * *</b></button>
                    </div>
                </div>
                {this.state.popup ? this.renderPopUp() : null}
            </div>
        );
    }
}

export default Bubble;